html {
  font-size: 100%;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--font-inter);
  background: var(--bg-color);
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  color: var(--color);
}

.visually-hidden {
  position: absolute;
  inset-inline-start: -625rem;
  top: auto;
  width: 0.0625rem;
  height: 0.0625rem;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

:root {
  --bg-color: hsl(180, 20%, 95%);
  --card-bg-color: hsl(0, 0%, 100%);
  --color: hsl(240, 5%, 15%);
  --primary-accent: hsl(180, 70%, 40%);
  --primary-accent-hover: hsl(180, 70%, 35%);
  --restart-btn: hsl(180, 15%, 85%);
  --restart-btn-hover: hsl(180, 15%, 80%);
  --success: hsl(120, 50%, 40%);
  --history-color: hsl(180, 50%, 35%);
}

[data-theme=dark] {
  --bg-color: hsl(180, 10%, 15%);
  --card-bg-color: hsl(180, 10%, 22%);
  --color: hsl(2, 0%, 90%);
  --primary-accent: hsl(180, 70%, 55%);
  --primary-accent-hover: hsl(180, 70%, 60%);
  --restart-btn: hsl(180, 10%, 35%);
  --restart-btn-hover: hsl(180, 10%, 30%);
  --success: hsl(120, 70%, 60%);
  --history-color: hsl(180, 60%, 75%);
}

@media (prefers-color-scheme: dark) {
  html:not([data-theme]) {
    --bg-color: hsl(180, 10%, 15%);
    --card-bg-color: hsl(180, 10%, 22%);
    --color: hsl(2, 0%, 90%);
    --primary-accent: hsl(180, 70%, 55%);
    --primary-accent-hover: hsl(180, 70%, 60%);
    --restart-btn: hsl(180, 10%, 35%);
    --restart-btn-hover: hsl(180, 10%, 30%);
    --success: hsl(120, 70%, 60%);
    --history-color: hsl(180, 60%, 75%);
  }
}
:root {
  --font-inter: 'Inter', sans-serif;
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
}

a {
  text-decoration: none;
}

input,
button {
  border: none;
}

.container {
  max-width: 28.125rem;
  padding: 2rem;
  border-radius: 1rem;
  background: var(--card-bg-color);
  align-self: center;
}

.form {
  margin-bottom: 2.5rem;
}
.form input {
  font-size: 1.25rem;
  width: 100%;
  border-radius: 0.5rem;
  margin-bottom: 1.25rem;
  padding: 1.125rem 5.625rem;
}
.form__button {
  display: flex;
  gap: 1rem;
  justify-content: center;
}
.form__button button {
  font-size: 1rem;
  padding: 0.75rem 1.5rem;
  border-radius: 1.5rem;
}
.form__button button[type=submit] {
  background: var(--primary-accent);
}
.form__button button[type=submit]:hover {
  background: var(--primary-accent-hover);
}
.form__button button[type=reset] {
  background: var(--restart-btn);
}
.form__button button[type=reset]:hover {
  background: var(--restart-btn-hover);
}

.header {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row-reverse;
}
@media (max-width: 39.9375em) {
  .header {
    align-items: start;
    flex-direction: column-reverse;
  }
}
.header__title {
  font-weight: 700;
}

.toggle {
  outline: none;
  border: none;
}
.toggle__wrapper {
  position: relative;
}
.toggle__button {
  width: to-rem(240);
  height: to-rem(240);
  border-radius: 50%;
}
.toggle__background {
  position: absolute;
}

.card {
  background: var(--card-bg-color);
}
.card__history__past-guesses, .card__history__correct-games {
  margin-bottom: 0.5rem;
}
.card__history__past-guesses h2, .card__history__correct-games h2 {
  margin-bottom: 1rem;
}
.card__history__past-guesses ul li, .card__history__correct-games ul li {
  margin-bottom: 0.5rem;
}
.card__history__past-guesses {
  margin-bottom: 2.5rem;
}